
.faculty_apply_leave_form_conatiner{
    margin-top: 8px;
    margin-bottom: 20px;
}
.faculty_apply_leave_form_heading{
    font-family: 'Poppins';
font-style: normal;
font-weight: 600;
font-size: 19px;
line-height: 16px;
color: #000000;
}
.faculty_apply_leave_form_div{
   display: grid;
  grid-template-columns: 1fr 1fr;
  gap:10px 25px;
  padding-top: 18px;

}
.faculty_apply_leave_form_div>div{
    display: flex;
    flex-direction: column;
}
.faculty_apply_leave_form_div>div>label{ 
font-family: 'Poppins';
font-style: normal;
font-weight: 500;
font-size: 14px;
line-height: 16px;
color: #000000;
text-transform: uppercase;
margin-bottom: 6px;
}

.date_picker{
width: 100%;
height:38px ;
background: #FFFFFF;
border: 0.3px solid lightgray;
border-radius: 4px;
padding: 0px 10px; 
}
.date_picker:hover{
outline: none;
 border: 0.5px solid #4096ff;;
}
.date_picker:focus{
   outline: none;
  border: 0.5px solid #4096ff;
}

.faculty_apply_leave_textarea_div>textarea:focus{
    outline: none;
    border: 0.5px solid #4096ff;;
}
.faculty_apply_leave_textarea_div>textarea:hover{
    outline: none;
    border: 0.5px solid #4096ff;
}
.faculty_apply_leave_form_div>div:last-child>label>span{ 
font-size: 12px;
text-transform: capitalize;
}
.faculty_apply_leave_textarea_div{
    display: flex;
    flex-direction: column;
    margin-top: 10px;
}
.faculty_apply_leave_textarea_div>label{
   font-family: 'Poppins';
font-style: normal;
font-weight: 500;
font-size: 14px;
line-height: 16px;
color: #000000;
text-transform: uppercase;
margin-bottom: 6px;
}
.faculty_apply_leave_textarea_div>textarea{
    border: 0.5px solid lightgrey;
    border-radius: 8px;
    padding: 8px 10px;
}
.faculty_apply_leave_upload_document{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top:25px;
}
.faculty_apply_leave_upload_document>div{
    width: 340px;
}
.faculty_apply_leave_upload_document>div>.ewqTBN{
    width: 100%;
    border: 1.5px solid rgb(6,88,194) !important;
    height: 70px;
}
.faculty_apply_leave_submit_btn{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top:32px;
    /* margin-bottom: 60px; */
}
.faculty_apply_leave_submit_btn>button{
background: #37B822;
border-radius: 5px;
border: none;
width: 150px;
height: 30px;
cursor: pointer;
font-family: 'Poppins';
font-style: normal;
font-weight: 500;
font-size: 13px;
line-height: 16px;
color: #FFFFFF;
text-transform: uppercase;
box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
transition: 0.4s ease-in-out;
}
.faculty_apply_leave_submit_btn>button:hover{
background: #5de847;
}
