.sidebar-container{
    width: 100%;
    height: auto;
    min-height: 85vh;
    padding: 30px 8px;
}
.nav-ul{
    display: block;
}
.nav-ul li{  
    list-style: none;  
    font-size: 15px;
}
.nav-ul li a{
    height: 35px;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding:5px 10px ;
    margin-bottom: 8px;
    border-radius: 4px;
    font-weight: 500;
    color: #5E17EB;
}
.nav-ul li a>span:first-child{
    display: flex;
    align-items: center;
    justify-content: center;
}
.nav-ul li a:hover{
  background-color: #f6f4ff;
}
.profile-icon{
    font-size: 25px;
}


/* ---------------------media query for mobile and tab------------------- */
@media screen and (max-width:920px){
    .nav-ul{
    display: none !important;
}
}