.holidays_add_btn_div{
    /* border: 1px solid red; */
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}
.holidays_add_btn_div>h3{
    color:gray;
    font-family: 'Poppins',sans-serif;
    font-weight: 600;
}
.holidays_add_btn_div>button{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 90px;
    height: 35px;
    background-color: rgb(82, 82, 243);
    color:#fff;
    border: none;
    outline: none;
     font-family: "Poppins",sans-serif;
     font-size: 21px;
     cursor: pointer;
     border-radius: 5px; 
     transition: 0.4s ease;  
}
.holidays_add_btn_div>button:hover{
     background-color: rgb(49, 49, 199);
}
.admin_holiday_create_div{
    position: absolute;
    right: 0;
    left: 0;
    bottom: 0;
    top: 0;
    /* opacity: 0.3; */
}
.admin_holiday_create{
    width: 300px;
    height: 240px;
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    background-color: #fff;
    z-index: 99;
    opacity: 1;
    padding: 25px 12px;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

}
.create_input_div>input{
    width: 100%;
   height: 33px;
   outline: none;
   border: none;
   border: 1px solid rgb(174, 174, 174);
   border-radius: 4px;
   margin-bottom: 15px;
   padding-left: 10px;
   padding-right: 10px;
   color: rgb(71, 71, 71);
   font-family: 'Poppins',sans-serif;
   font-weight: 400;
}
.create_holiday_data_btn{
    width:100%;
    margin-top: 15px;
    display: flex;
    justify-content: space-between;
}
.admin_holidays_list_div{
    height: 500px;
    border: 1px solid rgb(210, 210, 210);
    border-radius: 8px;
    padding: 10px;
    position: relative;
}
.holiday_loader_loading{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}
.admin_holidays_container{
    height: 440px;
    overflow-y: scroll;
}
.admin_holidays_list_data{
    display: flex;
    justify-content: space-between;
    border-bottom: 0.5px solid lightgray;
}
.admin_holidays_list_data:hover{
    background-color: lightgray;
}
.admin_holidays_list_data>span{
    flex:1;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 6px 0px;
    text-align: center;
}
.admin_holidays_list_data>span:first-child{
    font-size: 14px;
    text-transform: capitalize;
}
.admin_holidays_list_data>span:nth-child(2){
    font-size: 14px;
     text-transform: capitalize;
}
.admin_holidays_list_data>span:nth-child(3){
 font-size: 14px;
}
.holidays_edit_delete_btn_in_admin>span:first-child{
    font-size: 22px;
    margin-right: 6px;
    cursor: pointer;
    color:rgb(81, 81, 234)
}
.holidays_edit_delete_btn_in_admin>span:last-child{
    font-size: 22px;
    margin-left: 6px;
    cursor: pointer;
    color:rgb(232, 93, 93)
}
.holiday_data_edit_div{
    background-color: #fff;
    /* box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px; */
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
    width: 300px;
    height: 250px;
    /* overflow-x: scroll; */
    position: absolute;
    left: 50%;
    top:50%;
    transform: translate(-50%,-50%);
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.holiday_input_div{
    width: 250px;
}
.holiday_input_div>input{
    width: 100%;
    height: 33px;
    margin-bottom: 15px;
    border-radius: 5px;
    border: 1px solid lightgray;
    outline: none;
    padding-left: 8px;
    color: rgb(71, 71, 71);
   font-family: 'Poppins',sans-serif;
   font-weight: 400;
}
.holiday_edit_btn_div{
    width: 250px;
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
}