.login-container {
  width: 100%;
  height:100vh;
  /* overflow-y: hidden; */
  display: flex;
}
.login-left-div,
.login-right-div {
  flex: 1;
}
.login-right-div-img-container{
  width: 100%;
  height: 100%;
  overflow-y: hidden;
}

.login-left-div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.login-left-div>form{
 display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; 
}
.login-right-div img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  background-position: center;
}
.logo-div {
  width: 90px;
}
.logo-div img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.login-heading {
  text-transform: uppercase;
  font-style: normal;
  font-weight: 600;
  font-size: 23px;
  line-height: 48px;
  color: #000000;
}
.login-input-div {
  display: flex;
  width: 360px;
  height: 35px;
  background: #ffffff;
  border-radius: 4px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  margin-top: 12px;
}
.login-input-div:hover{
  border: 0.5px solid #4096ff;
}
.login-input-div span {
  width: 10%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: gray;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}
.login-input-div input {
  width: 90%;
  height: auto;
  border: none;
  outline: none;
  padding-left: 12px;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 500;
  color: #000;
  font-family: 'Poppins', sans-serif;
  cursor: pointer;
}
.login-input-div input[type="email"],input[type="text"],input[type="password"] {
  font-size: 15px;
  line-height: 21;
  font-weight: 400;
  color: #000;
}
.login-input-div input[type="date"]{
  color:gray;
}

.get-otp-btn-div {
  text-align: center;
}
.get-otp-btn {
  width: 130px;
  height:35px;
  /* padding: 10px 90px; */
  background: #3061f1;
  border-radius: 4px;
  border: none;
  margin-top: 20px;
  font-weight: 700;
  font-size: 14px;
  line-height: 21px;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 1px;
  cursor: pointer;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}
.login_data{
  margin-top: 15px;
  font-size: 14px;
  font-weight: 600;
   font-family: 'Poppins', sans-serif;
}
.login_data>div>span:last-child{
  color: red;
}

/* -------------------------------media query for mobile size----------------------------- */
@media screen and (max-width:320px){
   .login-container{
    padding-top: 100px;
  }
  .logo-div {
    width: 95px;
    margin-bottom: 11px;
  }
  .login-left-div {
    display: flex;
    margin-top: 40px !important;
    justify-content: flex-start;
  }
  .login-heading {
    font-size: 20px;
  }
  .login-right-div {
    display: none;
  }
  .login-input-div {
    display: flex;
    width: 260px;
    height: 45px;
    margin-bottom: 5px;
  }
  .input_form_container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  .login-input-div input[type="email"],input[type="text"],input[type="password"]{
    font-size: 17px;
  }
  .passHideShowBtn{
    font-size: 25px;
    margin-right: 6px;
  }

  .resend-otp-div {
    margin-top: 18px;
  }
  
  .get-otp-btn {
    width:150px;
    height: 35px;
    background: #3061f1;
    font-weight: 600;
    font-size: 16px;
  }
}

