.admin_reg_toast{
    height: 100px !important;
    font-size: 10px;
}
.dashboard_add_emp_page{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    /* padding-top: 15px; */
    padding-bottom: 20px;
}
.dashboard_add_emp_page>h3{
    font-weight: 600;
    font-family: 'Poppins',sans-serif;
    margin-bottom: 12px;
    font-size: 20px;
}
.dashboard_add_emp_input_container{
    width: 350px;
    display: flex;
    flex-direction: column;
}
.dashboard_add_emp_input_container>input{
    height: 38px;
    margin-bottom: 10px;
    outline: none;
    border: 0.5px solid lightgray;
    padding-left: 10px;
    border-radius: 4px;
    font-size: 14px;
    font-weight: 300;
  /* color: #000; */
  font-family: 'Poppins', sans-serif;
  /* color:rgb(180, 178, 178); */
}
.dashboard_add_emp_input_container>input:hover{
    border: 0.5px solid #4096ff;
    transition: 0.4s;
    cursor: pointer;
}
.dash_date_picker{
     height: 38px;
    margin-bottom: 10px;
    outline: none;
    border: 0.5px solid lightgray;
    padding-left: 10px;
    border-radius: 4px;
    font-size: 14px;
    font-weight: 400;
  color: #000;
  font-family: 'Poppins', sans-serif;
}
.ant-picker .ant-picker-input>input::placeholder {
     font-size: 14px;
    font-weight: 500;
    color:rgb(180, 178, 178);
  /* color: #000; */
}
.dash_dropdown{
    width: 350px;
     height: 38px;
    margin-bottom: 5px;
     color: #000;
}
.dash-select-box{
    width: 100%;
    outline: none;
    font-size: 14px;
    font-weight: 600 !important;
  color: #000;
  font-family: 'Poppins', sans-serif;
}
.ant-select-selector{
    border-radius: 4px !important;
    outline: none;
    border: none;
     font-weight: 300 !important;
    font-size: 15px !important;
    height: 38px !important;
    /* color: gray !important; */
    font-family: 'Poppins', sans-serif !important;
    padding: 2px 10px !important;
    color:rgb(180, 178, 178) !important;
}
.dash_reg_btn{
    text-align: center;
    margin-top: 15px;
}
.dash_reg_btn>button{
    width: 120px;
    border: none;
    outline: none;
    background-color: #3785e6;
    color: #fff;
    height: 40px;
    font-size: 16px;
    border-radius: 20px;
    letter-spacing: 0.5px;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
}
.dash_reg_btn>button:hover{
   background-color: #0a4d9e; 
   cursor: pointer;
   transition: 0.4s;
}