.dashboard_emp_page_container{
    margin: 8px;
}
.emp_search_container{
    height: 60px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
}
.emp_search_box,.emp_add_div{
    flex: 1;
}

.emp_search_box{
    height: 60px;
    display: flex;
    align-items: center;
    padding-left: 20px;
}
.emp_search_box>input{
    width: 300px;
    height: 35px;
    padding:3px 10px;
    border: none;
    outline: none;
    background-color: #fff;
    border:0.5px solid rgb(170, 166, 166) ;
    border-left: 0.5px solid rgb(170, 166, 166) ;
    border-bottom: 0.5px solid rgb(170, 166, 166) ;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
}
.emp_search_box>input[type="text"]{
    font-size: 13px;
    font-weight: 400;
    font-family: "Poppins",sans-serif;
}
.emp_search_box>span{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 35px;
    min-height: 35px;
    font-size: 25px;
    background-color: rgb(82, 82, 243);
    color:#fff;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    cursor: pointer;
}
.emp_search_box>span:hover{
    background-color: rgb(42, 42, 240);
}
.emp_add_div{
    display: flex;
    align-items: center;
   justify-content: end;
   padding-right: 20px;
}
.emp_add_div>button{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100px;
    height: 35px;
    background-color: rgb(82, 82, 243);
    color:#fff;
    border: none;
    outline: none;
     font-family: "Poppins",sans-serif;
     font-size: 17px;
     cursor: pointer;
     border-radius: 5px;
}
.emp_add_div>button:hover{
     background-color: rgb(42, 42, 240);
}
.emp_add_div>button>span{
    display: flex;
    align-items: center;
    justify-content: center;
   font-size: 20px;
}
.emp_list_container{
    height: 400px;
    /* margin-top: 22px; */
}
.total_reg_users_count{
    font-size: 13px;
    font-weight: 600;
    font-family: 'Poppins',sans-serif;
}
.emp_list_div{
    height:450px;
    background-color: #fff;
    border: 1px solid gray;
    padding: 15px;
    border-radius: 6px;
    position: relative;
}

.emp_list_heading{
    display: flex;
    margin: 0px 8px;
      /* gap:8px; */
}
.emp_list_heading>span{
    flex: 1;
    font-weight: 500;
    font-family: "Poppins",sans-serif;
    font-size: 15px;
    text-align: center;
}

.user_list_container{
    width: 100%;
    height: 400px;
    overflow-y: scroll;
    scroll-behavior: smooth;
}
.emp_list_data{
    display: flex;
    border-bottom: 0.5px solid lightgray;
    padding: 6px 0px;
}
.emp_list_data:hover{
    background-color: lightgray;
}
.emp_list_data>span{
    flex: 1;
    font-weight: 400;
    font-family: "Poppins",sans-serif;
    font-size: 13px;
    margin-top: 6px;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}
.emp_list_data>span:first-child{
  display: flex;
  align-items: center;
  justify-content: flex-start !important;
}
/* ---------------------------------------------------------------- */
.name_and_img_span{
    display: flex;
    justify-content: center;
}

.dashboard_emp_img{
    width: 30px;
    height: 30px;
    border-radius: 100%;
    margin-right: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.dashboard_emp_img>img{
    width: 30px;
    height: 30px;
     border-radius: 100%;
}
.table_user_icon{
 width: 30px;
height: 30px;
display: flex;
align-items: center;
justify-content: center;
font-size: 38px;
color:gray
}
.dashboard_action_button>span{
    font-size: 22px;
}
.dashboard_action_button>span:first-child{
    color: rgb(82, 82, 243);
    margin-right: 5px;
    cursor: pointer;
}
.dashboard_action_button>span:last-child{
    color:rgb(232, 47, 47);
    margin-left: 5px;
    cursor: pointer;
}
.dashboard_loading,.no_data_div{
   position: absolute;
   left: 50%;
   top: 50%;
   transform: translate(-50%,-50%);
}
.no_data_div>p{
    font-size: 23px;
}
.dashboard_emp_profile_div{
    background-color: #fff;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
    width: 300px;
    height: 350px;
    overflow-x: scroll;
    position: absolute;
    left: 50%;
    top:50%;
    transform: translate(-50%,-50%);
    z-index: 99;
    transition: 0.4s ease-in-out;
}
.dashboard_emp_profile_div::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.dashboard_emp_profile_div {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}
.emp_profile_close_btn{
    display: flex;
    justify-content: end;
}
.emp_profile_close_btn>span{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    padding:2px;
    border: 0.5px solid lightgrey;
    cursor: pointer;
}
.emp_profile_close_btn>span:hover{
 background-color: lightgray;
}
.emp_profile_details{
    margin-top: 15px;
}
.emp_profile_details_data{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* border: 1px solid red; */
}
.dash_details_img{
    width: 60px;
    height:60px;
    border-radius: 100%;
    margin-bottom: 15px;
}
.dash_user_details_icon{
     width: 60px;
    height:60px;
    font-size: 75px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: gray;
    
}
.dash_details_img>img{
    width: 60px;
    height: 60px;
     border-radius:100% ;
     object-fit: cover;
     background-position: center;
}
.emp_datas>p:first-child{
    font-weight: 500;
    text-align: center;
}
.emp_data{
    text-align: center;
    /* border: 1px solid red; */
}
.emp_data>span{
    font-size: 12px;
    font-family: 'Poppins',sans-serif;
}
.emp_data>span:first-child{
    font-weight: 500;
    font-size: 14px;
    font-family: 'Poppins',sans-serif;
}
.dash_delete_popup_container{
     background-color: #fff;
     width: 250px;
     height: 110px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    position: absolute;
    left: 50%;
    top:50%;
    transform: translate(-50%,-50%);
    z-index: 99;
}
.dash_delete_popup_div{
    padding: 15px 10px;
}
.dash_delete_popup_div>p{
    font-size: 14px;
    font-family: 'Poppins',sans-serif;
    font-weight: 500;
    text-align: center;
}
.dash_yes_no_btn_div{
    margin-top: 25px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.name_and_img_span>span:last-child{
    text-transform: capitalize;
}