.dash_report_container{
    margin: 8px;
}
.report_search_container{
    height: 80px;
    display: flex;
    align-items: center;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    margin: 8px;
     margin-bottom: 22px;
    
}
/* .report_search_box,.report_add_div{
    flex: 1;
} */
.report_dropdown_div{
    width:200px;
    margin-left: 20px;
}
.report-search-main-div{
    display: flex;
    align-items: center;
    gap: 10px;
    margin-left: 30px;
}
.report_search_box{
    width: 200px !important;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    /* border: 1px solid red; */
}
.to_class{
    font-weight: 500;
    font-family: 'poppins',sans-serif;
    font-size: 15px;
    /* margin: 0px 5px; */
    color: #7b4adc;
    text-transform: uppercase;
}
.report_search_box>input{
    width: 200px;
    height: 38px;
    padding:3px 10px;
    border: none;
    outline: none;
    background-color: #fff;
    border:0.5px solid rgb(221, 220, 220) ;
    border-radius: 4px;
}
.report_search_box>input[type="date"]{
    font-size: 15px;
    font-weight: 400;
    font-family: "Poppins",sans-serif;
    color:rgb(156, 154, 154);
}
.report_search_box>input[type="text"]{
    font-size: 15px;
    font-weight: 400;
    font-family: "Poppins",sans-serif;
    color:rgb(227, 225, 225);
}
.report_search_box>input:hover{
outline: none;
 border: 0.5px solid #4096ff;;
}
.report_search_box>input:focus{
   outline: none;
  border: 0.5px solid #4096ff;
}
.go_btn{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 35px;
    border: none;
    outline: none;
    color: gray;
    border-radius: 7px;
    font-size: 30px;
    cursor: pointer;
    background-color: transparent;
}
.go_btn:hover{
    color: #5E17EB;
    transition: 0.4s;
}

.report_add_div{
    flex: 1;
    display: flex;
    align-items: center;
   justify-content: end;
   padding-right: 20px;
    margin-left: 8px;
}
.report_add_div>button{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 125px;
    height: 35px;
    background-color: rgb(82, 82, 243);
    color:#fff;
    border: none;
    outline: none;
     font-family: "Poppins",sans-serif;
     font-size: 16px;
     cursor: pointer;
     border-radius: 5px;
     box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}
.report_add_div>button:hover{
   background-color: rgb(100, 100, 235); 
}
.dash_download_icon{
    color:#fff !important;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 6px;
    font-size: 16px;
}